import Head from 'next/head'
import Navigation from '../components/Navigation'
import React, { Component } from 'react'
import axios from 'axios';
import { Fragment } from 'react'
import "../style.css"

export default class extends Component {

  // Resolve promise and set initial props.
  static async getInitialProps( context ) {

    const slug = context.query.slug

    // Make request for posts.
    const response = await axios.get( `http://localhost:8888/codeformylife/wp-json/wp/v2/posts?slug=${ slug }` )
    console.log(response)

    // Return our only item in array from response to posts object in props.
    return {
      post: response.data[0]
    }
  }

  render() {
    return (
      <Fragment>
        <Head>
          <title>{ this.props.post.title.rendered }</title>
          <meta name="description" content={ `This is a single post for ${ this.props.post.title.rendered }` } />
          <meta charSet="utf-8" />
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>
        <Navigation/>
            <div className="w3-container w3-centered">
              <div className="w3-row">
                <div className="w3-col l8 s12 w3-card-4 w3-margin w3-white">
                  <h1>{ this.props.post.title.rendered }</h1>
                  
                  <div className="w3-container">
                      <article
                              className="entry-content"
                              dangerouslySetInnerHTML={ {
                              __html: this.props.post.content.rendered
                          } } />
                  </div>
                </div>    
                </div> 
             </div>
        
      </Fragment>
    )
  }
}